import React, { useCallback, useState } from 'react';
import { Button, Container, Grid, Link, TextField, Typography } from '@mui/material';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import {join} from '../apis/userApi';

const Join = () => {
    const [form, setForm] = useState({
        userId: '',
        userPw: '',
        userPwChk: '',
        userName: '',
        userEmail: '',
        userTel: ''
    });
    const [idChk, setIdChk] = useState(false);
    const [pwValidation, setPwValidtaion] = useState(false);
    const [pwChk, setPwChk] = useState(false);
    const dispatch = useDispatch();

    const textFiledchanged = useCallback((e) => {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        });

        if(e.target.name === 'userId') {
            setIdChk(false);
            document.querySelector("#userIdChk").removeAttribute('disabled');
            return;
        }


        //   
        if(e.target.name === 'userPw') {
            if(e.target.value === form.userPwChk) {
                setPwChk(true);
                document.querySelector("#password-check-success").style.display = 'block';
                document.querySelector("#password-check-fail").style.display = 'none';
            } else {
                setPwChk(false);
                document.querySelector("#password-check-success").style.display = 'none';
                document.querySelector("#password-check-fail").style.display = 'block';
            }

            return;
        }

        if(e.target.name === 'userPwChk') {
            if(e.target.value === form.userPw) {
                setPwChk(true);
                document.querySelector("#password-check-success").style.display = 'block';
                document.querySelector("#password-check-fail").style.display = 'none';
            } else {
                setPwChk(false);
                document.querySelector("#password-check-success").style.display = 'none';
                document.querySelector("#password-check-fail").style.display = 'block';
            }

            return;
        }
    }, [form]);

    // id  
    const idCheck = useCallback(async () => {
        if(form.userId === '') {
            alert(" .");
            document.querySelector("#userId").focus();
            return;
        }

        try {
            const response = await axios.post(
                `http://localhost:9090/user/id-check`,
                {
                    userId: form.userId
                }
            );

            if(response.data.item.idCheckResult === 'invalid id') {
                alert(" .   .");
                document.querySelector("#userId").focus();
                return;
            } else {
                if(window.confirm(`${form.userId}  . ?`)) {
                    document.querySelector("#userIdChk").setAttribute('disabled', true);
                    setIdChk(true);
                    return;
                }
            }
        } catch(e) {
            console.log(e);
            alert(" .  .");
        }
    }, [form.userId]);

    //    
    const validatePassword = (userPw) => {
        return /^(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[!@#$%^&*+=-]).{9,}$/.test(userPw);
    }

    //       
    const userPwBlur = useCallback((e) => {
        if(validatePassword(e.target.value)) {
            setPwValidtaion(true);
            document.querySelector("#password-validation").style.display = "none";
            return;
        } else {
            setPwValidtaion(false);
            document.querySelector("#password-validation").style.display = "block";
            document.querySelector("#userPw").focus();
            return;
        }
    }, []);

    //  
    const handleJoin = useCallback((e) => {
        e.preventDefault();

        if(!idChk) {
            alert("  .");
            return;
        }

        if(!pwValidation) {
            alert(" , ,   9  .");
            document.querySelector("#userPw").focus();
            return;
        }

        if(!pwChk) {
            alert("  .");
            document.querySelector("#userPwChk").focus();
            return;
        }

        dispatch(join(form));
    }, [form, idChk, pwValidation, pwChk, dispatch]);
  return (
    <Container component='div' maxWidth="xs" style={{marginTop: '8%'}}>
        <form onSubmit={handleJoin}>
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Typography component='h1' variant='h5'>
                        
                    </Typography>
                </Grid>
                <Grid item xs={12} textAlign='right'>
                    <TextField
                        name='userId'
                        variant='outlined'
                        required
                        id='userId'
                        label=''
                        autoFocus
                        fullWidth
                        value={form.userId}
                        onChange={textFiledchanged}
                    ></TextField>
                    <Button name='userIdChk' id='userIdChk' color='primary' onClick={idCheck}>
                        
                    </Button>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        name='userPw'
                        variant='outlined'
                        required
                        id='userPw'
                        label=''
                        fullWidth
                        type='password'
                        value={form.userPw}
                        onChange={textFiledchanged}
                        onBlur={userPwBlur}
                    ></TextField>
                    <Typography
                        name='password-validation'
                        id='password-validation'
                        component='p'
                        variant='string'
                        style={{display: 'none', color: 'red'}}>
                         , ,   9  .
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        name='userPwChk'
                        variant='outlined'
                        required
                        id='userPwChk'
                        label=' '
                        fullWidth
                        type='password'
                        value={form.userPwChk}
                        onChange={textFiledchanged}
                    ></TextField>
                    <Typography
                        name='password-check-success'
                        id='password-check-success'
                        component='p'
                        variant='string'
                        style={{display: 'none', color: 'green'}}>
                         .
                    </Typography>
                    <Typography
                        name='password-check-fail'
                        id='password-check-fail'
                        component='p'
                        variant='string'
                        style={{display: 'none', color: 'red'}}>
                          .
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        name='userName'
                        variant='outlined'
                        required
                        id='userName'
                        label=''
                        fullWidth
                        value={form.userName}
                        onChange={textFiledchanged}
                    ></TextField>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        name='userEmail'
                        variant='outlined'
                        required
                        id='userEmail'
                        label=''
                        fullWidth
                        type='email'
                        value={form.userEmail}
                        onChange={textFiledchanged}
                    ></TextField>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        name='userTel'
                        variant='outlined'
                        id='userTel'
                        label=''
                        fullWidth
                        value={form.userTel}
                        onChange={textFiledchanged}
                    ></TextField>
                </Grid>
                <Grid item xs={12}>
                    <Button
                        type='submit'
                        fullWidth
                        variant='contained'
                        color='primary'>
                        
                    </Button>
                </Grid>
            </Grid>
            <Grid container justifyContent='flex-end'>
                <Grid item>
                    <Link href="/login" variant='body2'>
                           .
                    </Link>
                </Grid>
            </Grid>
        </form>
    </Container>
  );
}

export default Join;